* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: rgb(233, 236, 238);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: black;
}
h1 {
  color: brown;
}
.blacklist {
  width: 90vw;
  max-width: 1111px;
  margin: 30px auto;
  display: grid;
  gap: 6rem;
}
@media screen and (min-width: 369px) {
  .blacklist {
    grid-template-columns: repeat(3, 1fr);
  }
}
.book {
  background: rgb(255, 255, 255);
  border-radius: 2px;
  padding: 1rem 2rem;
}
.book h1 {
  margin-top: 1rem;
}
.book h4 {
  margin-top: 0.25rem;
  font-size: 0.75rem;
  color: rgb(52, 133, 165);
}
.book p {
  margin-top: 0.5rem;
}
